.outbar{
    width: 80vw;
    height: 2vh;
    margin: 2%;
    background-color: var(--white);
    border: 0.1vh solid var(--black);
    text-align: end;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bolder;
    color: var(--graph);
}

.innerbar{
    height: inherit;
    background-color: var(--graph);
}
.languages{
        color: var(--black);
        text-align: end;
        font-family: 'Montserrat', sans-serif;
        font-size: 1rem;
        font-weight: 800;
        padding: 1%;
        max-width: 50%;
        align-items: center;
}